.typewriter__wrapper {
    font-size: 2.2em;
    color: #be6adf;
    font-weight: 600;
}

.typewriter__cursor {
    font-size: 2.4em;
    color: #b562d6;
}

@media (max-width: 767px) {
    .typewriter__wrapper {
        font-size: 1.4em;
        font-weight: 500;
        position: absolute;
    }

    .typewriter__cursor {
        display: none;
    }
}