/* --------- */
/* Home section */
/* --------- */

.header {
    padding-top: 80px;
}

.section {
    position: relative;
    z-index: -1;
    background-image: var(--image-gradient), url(../../assets/pixels.png);
    background-position: bottom center;
    background-repeat: no-repeat;
    padding-bottom: 100px;
}

.content {
    padding: 9rem 0 2rem;
    color: whitesmoke;
    text-align: left;
}

.heading {
    font-size: 2.4em;
    padding-left: 50px;
}

.heading-name {
    font-size: 2.5em;
    padding-left: 45px;
}

.main-name {
    color: #cd5ff8;
}

