/* --------- */
/* Navbar Section  */
/* --------- */
.sticky {
    background-color: #212038d8;
    transition: all 0.3s ease-out 0s;
    box-shadow: 0 10px 10px 0 rgba(9, 5, 29, 0.171);
    backdrop-filter: blur(15px);
    font-size: 1.2rem;
    padding: 0.3rem 2rem;
}

.navbar {
    position: fixed;
    transition: all 0.3s ease-out 0s;
    padding: 0.3rem 2rem;
    font-size: 1.2rem;
}

.toggler {
    padding: 0.25rem 1.5rem;
    position: relative;
    background-color: transparent;
    border-color: transparent;
}

.toggler span {
    display: block;
    background-color: #be50f4;
    height: 4px;
    width: 27px;
    margin-top: 5px;
    margin-bottom: 5px;
    transform: rotate(0deg);
    left: 0;
    opacity: 1;
}

.toggler:focus,
.toggler:active {
    outline: 0;
}

.toggler span:nth-child(1),
.toggler span:nth-child(3) {
    transition: transform 0.35s ease-in-out;
}

.toggler:not(.collapsed) span:nth-child(1) {
    position: absolute;
    left: 12px;
    top: 10px;
    transform: rotate(135deg);
    opacity: 0.9;
}

.toggler:not(.collapsed) span:nth-child(2) {
    height: 12px;
    visibility: hidden;
    background-color: transparent;
}

.toggler:not(.collapsed) span:nth-child(3) {
    position: absolute;
    left: 12px;
    top: 10px;
    transform: rotate(-135deg);
    opacity: 0.9;
}

@media (max-width: 767px) {
    .navbar {
        padding: 1rem 2rem;
        font-size: 1.4rem;
        background-color: #181a27;
    }

    .nav .item a::after {
        display: none;
    }
}

.brand {
    padding-top: 0 !important;
    color: rgb(250, 250, 250);
    height: 2.4em;
    width: 2.5em;
}

.logo {
    filter: invert(1);
    height: 2.5em;
    width: 2.5em;
}

.nav .link {
    color: white;
    padding-right: 1rem;
    padding-left: 1rem;
}

.link {
    padding: 0.8rem 1rem;
}

@media (max-width: 767px) {
    .link {
        padding: 0.7rem 1rem;
    }
}

.nav .item {
    position: relative;
    margin-left: 20px;
    padding-right: 40px;
}

.nav .item a {
    font-weight: 400;
    transition: all 0.3s ease-out 0s;
    position: relative;
    z-index: 1;
}

.item a::after {
    content: "";
    position: relative;
    display: block;
    height: 5px;
    width: 0;
    border-radius: 16px;
    background: #c95bf5;
    bottom: 1px;
    left: 0;
    z-index: -1;
    transition: all 0.3s ease-out 0s;
    color: white;

}

.nav .item a:hover::after {
    width: 100%;
}

.fork {
    color: white;
    font-size: 1.1em;
    padding-top: 5px;
}

.fork-inner {
    width: 100px;
    line-height: 1.4em;
    background-color: #934cce5e;
    vertical-align: middle;
    text-align: center;
    transition: all 0.2s ease-out !important;
    transform: translateY(2px) !important;
}

.fork-inner:hover {
    transition: all 0.2s ease-out !important;
    transform: translateY(-2px) !important;
    background-color: #a24dd386;
    border-color: #a24dd386;
}