.my-avatar {
    justify-content: center;
    padding-top: 9em;
}

@media (max-width: 767px) {
    .my-avatar {
        padding-top: 2em;
        padding-bottom: 2em;
    }
}

.about-section {
    position: relative;
    padding-bottom: 70px;
}

.pixel-background {
    background-image: url(../../assets/reversed2.png);
    background-position: top ;
    background-repeat: no-repeat;
    background-size: 1921px 879px;
}

.about-description {
    color: white;
    padding-top: 100px;
    padding-bottom: 20px;
    text-align: center;
}

.about-body {
    padding-top: 50px;
    font-size: 1.2em;
    text-align: left;
}

.about-social {
    text-align: center;
    padding-top: 25px;
    color: white;
}

.about-social-links {
    justify-content: center;
    padding-top: 15px;
    display: inline-block;
    position: relative;
    padding-inline-start: 0;
}

.awesome-social-icons {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 40px;
    text-align: center;
    font-size: 1.2em;
    line-height: 43px;
    background: rgba(255, 255, 255, 0.972);
    border-radius: 50%;
    transition: 0.5s;
}

.awesome-social-icons::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: #68187a;
    transition: 0.5s;
    transform: scale(0.9);
    z-index: -1;
}

.awesome-social-icons:hover::before {
    transform: scale(1.1);
    box-shadow: 0 0 15px #801f95;
}

.awesome-social-icons:hover {
    color: #87209e;
    box-shadow: 0 0 5px #87209e;
    text-shadow: 0 0 2px #87209e;
}

.social-icons {
    display: inline-block;
    padding-right: 15px;
    padding-left: 15px;
}

.icon-colour {
    color: #700c86;
}